*, *::before, *::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
$main-color: linear-gradient(45deg, rgb(69, 80, 246) 0%, rgb(252, 71, 240) 100%);
body {
  #root {

  }
  font-family: "Montserrat", sans-serif;
  color: black;
}
h1 {
  font-size: 1.5rem;
  font-weight: 400;
  box-shadow: 0px 1px 1px rgb(140, 140, 140);
}

h2 {
  font-size: 1rem;
  font-weight: 300;
  box-shadow: 0px 1px 1px rgb(140, 140, 140);

}
.btn {
  // width: 100px;
  height: 30px;
  background: $main-color;
  border-radius: 10px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.5s;
  cursor: pointer;
  padding: 2px 5px;
  box-shadow: 1px 2px 5px rgb(122, 122, 122),
              -1px -2px 5px 2px rgb(161, 161, 161);
}
.main {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  flex-direction: column;
  gap: 50px;

}
ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 10px;
  height: 200px;
  width: 100px;
  overflow-y: scroll;
  li {
    border: 1px solid black
  }
}
.main-buttons {
  display: flex;
  gap: 10px;
}